.navigationItem {
  margin: 10px 0;
  display: block;
  width: 100%;
}

.navigationItem a {
  color: #8f5c2c;
  text-decoration: none;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
}

.navigationItem a:hover,
.navigationItem a:active,
.navigationItem a.active {
  color: #40a4cb;
}

@media (min-width: 500px) {
  .navigationItem {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: auto;
    -ms-flex-align: center;
        align-items: center;
  }

  .navigationItem a {
    color: white;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
  }

  .navigationItem a:hover,
  .navigationItem a:active,
  .navigationItem a.active {
    background-color: #99591e;
    border-bottom: 4px solid #17323b;
    color: white;
  }
}



